* {
    font-family: Arial, Helvetica, sans-serif;
    color: #dce0e6;
}
body {
    padding: 60px;
    background-color: rgb(0, 30, 60);
}

.card-grid{
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;

}

.card{
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
}

.card p {
    padding: 5px;
    text-align: center;
}

.card img {
 max-height: 170px;
}